.single-movie-container {
  width: 100%;
  height: 1375px;
  background-color: rgb(0, 0, 0);
}

@media only screen and (max-width: 768px) {
  .single-movie-container {
    height: max-content;
  }
}

.single-movie-image {
  width: 100%;
  height: 400px;
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;
  position: relative;
  font-family: 'CrimsonPro', sans-serif;
}

.single-movie-info {
  height: 450px;
  width: 100%;
  background-color: rgb(0, 0, 0);
}

@media only screen and (max-width: 768px) {
  .hero {
    height: 0;
  }

  .single-movie-info {
    margin-top: 90px;
  }
}

@media only screen and (max-width: 736px) {
  .single-movie-info {
    height: 400px;
    margin-top: 110px;
  }
}

@media only screen and (max-width: 667px) {
  .single-movie-info {
    height: 440px;
    margin-top: 70px;
  }
}

@media only screen and (max-width: 414px) {
  /* .single-movie-container {
    padding-left: 10px;
  } */
  .single-movie-info {
    flex-direction: column;
    height: 900px;
    justify-content: center;
    align-items: center;
  }
}

.single-movie-info > div:nth-child(1) {
  height: 100%;
  width: 30%;
  background-color: rgba(0, 0, 0, 0.7);
}

.single-movie-info > div:nth-child(2) {
  height: 100%;
  width: 70%;
}

.movie-details {
  font-family: sans-serif;
  color: rgb(234, 234, 234);
  padding: 50px;
  gap: 5px;
}

@media only screen and (max-width: 768px) {
  .movie-details {
    padding-top: 0;
  }
}

@media only screen and (max-width: 414px) {
  .single-movie-info > div:nth-child(2) {
    height: 100%;
    width: 100%;
    /* padding: 0 20px 0 20px; */
  }

  .single-movie-info {
    max-width: 100%;
  }
  .movie-details {
    padding: 15px;
  }
}

.movie-details > div span:nth-child(1) {
  font-size: 2.5rem;
  font-weight: 400;
  color: white;
  font-family: 'Montserrat', sans-serif;
}

.movie-details > div span:nth-child(2) {
  margin-left: 10px;
  color: rgb(154, 154, 154);
  font-size: 0.8rem;
  font-family: 'Source Serif 4', sans-serif;
}

.movie-details > span:nth-child(2) {
  color: rgb(158, 158, 158);
  font-family: 'Montserrat', sans-serif;
  font-style: italic;
  font-size: 0.8rem;
  font-family: 'Source Serif 4', sans-serif;
}

.movie-details > span:nth-child(3) {
  margin-top: 30px;
  color: rgb(158, 158, 158);
  font-family: 'Source Serif 4', sans-serif;
  font-size: 0.9rem;
  line-height: 1.3rem;
}

.movie-details > span:nth-child(4) b {
  color: rgb(198, 198, 198);
  font-family: sans-serif;
  margin-top: 2.5px;
  font-size: 0.8rem;
}

.movie-details > span:nth-child(4) {
  margin-top: 25px;
  color: rgb(158, 158, 158);
  font-family: 'Source Serif 4', sans-serif;
  gap: 5px;
  font-size: 0.8rem;
}

.movie-details > span:nth-child(5) {
  color: rgb(198, 198, 198);
  /* font-family: 'Source Serif 4', sans-serif; */
  gap: 5px;
  font-size: 0.8rem;
}

.movie-details > span:nth-child(5) span {
  color: rgb(158, 158, 158);
  font-family: 'Source Serif 4', sans-serif;
  gap: 5px;
  font-size: 0.8rem;
}

.cast {
  gap: 13px;
  font-size: 0.8rem;
}

.cast b {
  color: rgb(198, 198, 198);
  font-family: sans-serif;
  margin-top: 2.5px;
}

.cast > span {
  color: rgb(158, 158, 158);
  font-family: 'Source Serif 4', sans-serif;
  width: max-content;
  margin-right: 10px;
}

.related-txt {
  height: 50px;
  width: 100%;
  color: white;
  /* border-top: double rgb(99, 0, 0); */
  color: rgb(207, 34, 3);
  font-family: sans-serif;
  font-size: 1rem;
  padding: 10px;
  letter-spacing: 0.9rem;
  margin-top: 30px;
  background-color: rgb(10, 10, 10);
}

.related {
  height: 350px;
  overflow: auto;
  /* border-bottom: double rgb(99, 0, 0); */
  background-color: rgb(10, 10, 10);
  /* gap: 5px; */
}

.backdrop {
  height: 100vh;
  width: 100vw;
}
